


















































































































































import Responsive from '@mixins/Responsive'

import { IImage } from '@/lib/types'

import ButtonDefault from '@atoms/ButtonDefault/button-default.vue'
import SwitchDefault from '@/components/atoms/Switch/switch-default.vue'
import Auth from '@mixins/Auth'

export default Auth.extend({
  name: 'navbar-default',
  mixins: [
    Auth,
    Responsive
  ],
  props: {
    /**
    * Image object that contains the logo data.
    */
    logo: {
      type: Object as () => IImage,
      default: () => ({
        src: 'https://raw.githubusercontent.com/buefy/buefy/dev/static/img/buefy-logo.png',
        alt: 'Brand Logo',
        ratio: '1by1'
      } as IImage)
    },
    showLinks: {
      type: Boolean,
      default: true
    },
    useCustomToggle: {
      type: Boolean,
      default: true
    },
    withAuth: {
      type: Boolean,
      default: false
    },
    useDarkModeToggle: {
      type: Boolean,
      default: true
    },
    showSignedInAs: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      isOpen: false,
      showLogo: false
    }
  },
  created() {
    window.addEventListener('scroll', this.handleScroll)
  },
  beforeDestroy() {
    window.removeEventListener('scroll', this.handleScroll)
  },
  methods: {
    handleScroll() {
      const body = document.querySelector('html')
      const navbarBrand = document.querySelector('.navbar-brand')

      const scrollTop = body?.scrollTop

      if (!scrollTop || scrollTop <= 10) {
        this.showLogo = false
        navbarBrand?.classList.remove('logo-hidden')
      } else {
        this.showLogo = false
        navbarBrand?.classList.add('logo-hidden')
      }
    }
  },
  components: {
    ButtonDefault,
    SwitchDefault
  }
})
