












import Theme from '@/mixins/Theme'

export default Theme.extend({
  name: 'switch-default',
  mixins: [Theme],
  computed: {
    modeText() {
      return this.$store.getters.getTheme === 'dark'
    }
  },
  data() {
    return {
      darkMode: this.$store.getters.getTheme === 'dark'
    }
  },
})
